<template>
  <div class="container">
    <h3>Last</h3>
      <div class="create-post">
        <label for="create-post">Create</label>
        <input type="text" id="create-post" v-model="text">
        <button v-on:click="createPost">create</button>
      </div>
    <hr>
    <p class="error" v-if="error">{{error}}</p>
    <div class="posts-container">
      <div class="post"
      @dblclick="deletePost(post._id)"
      v-for="post in data" 
      :key="post._id"
      
      >
        {{`${post.date.getDate()}/${post.date.getMonth()}`}}
        <p class="text">
          {{post.text}}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from "../PostService";
export default {
  name: 'PostComponent',
  data(){
    return{
      data:[],
      error:"",
      text:"",
    }
  },
  async created(){
    try {
      this.data = await PostService.getData();
    } catch (error) {
      this.error = error.message;
    }
  },
  methods:{
    async createPost(){
      await PostService.insertData(this.text);
      this.data = await PostService.getData();
    },
    async deletePost(id){
      console.log("CLICK")
      await PostService.deleteData(id);
      this.data = await PostService.getData();
    },
  }
}
</script>

